<template>
    <section class="main-content">
        <loader :is-visible="isLoading"></loader>
        <div class="row">
            <div class="col-md-12 padding-t_8">
                <div class="theme-primary partition-full">
                    <span class="p-name text-white"> {{ $t('AccountInformation') }}: {{AccountName}}</span>
                    <span class="float-right">
                        <RouterLink to="/Client/Index" class="p-action-btn text-white float-right">
                            <em class="fa fa-angle-double-left"></em>
                            <br />
                            {{ $t('BacktoList') }}
                        </RouterLink>
                    </span> 
                </div>
            </div>
        </div>
        <div class="border p-3">
            <dynamic-form ref="dynamicform" lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="OnSubmit"></dynamic-form>
        </div>
    </section>
</template>
<script>
import DataServices from "../../services/DataService";
export default {
    data() {
        return {
            isLoading: false,
            MandatoryString:'Fields marked with an asterisk (*) are mandatory.',
            companyId: null,
            userId: null,
            AccountName:'',
            AccountProfileData:[],
            FormSchema: [{
                    layoutType: "four",
                    Data: [{
                        astype: "TextField",
                        label: this.$t('AccountName'),
                        name: "AccountName",
                        value: "",
                        placeholder: this.$t('Enter Account Name'),
                        validationRules: {
                            "required": true
                        },
                        disabled: false,
                    }, 
                    {
                        astype: "TextField",
                        label: this.$t('AccountType'),
                        name: "AccountType",
                        value: "customer",
                        placeholder: this.$t('Enter Account Type'),
                        validationRules: {
                            "required": false
                        },
                        disabled: true,
                    },
                    {
                        astype: "TextField",
                        label: this.$t('Website'),
                        name: "Website",
                        value: "",
                        placeholder: this.$t('Enter Website'),
                        validationRules: {
                            "required": false
                        },
                        disabled: false,
                    },
                    {
                        astype: "SelectField",
                        label: this.$t('Currency'),
                        name: "Currency",
                        value: "",
                        placeholder: "",
                        validationRules: {
                            "required": false
                        },
                        config: {
                            options: [],
                        },
                        visibility: true
                    }]
                },
                {
                    layoutType: "four",
                    Data: [{
                        astype: "TextField",
                        label: this.$t('AccountOwner'),
                        name: "AccountOwner",
                        value: "",
                        placeholder: this.$t('Enter Account Owner'),
                        validationRules: {
                            "required": false
                        },
                        disabled: true,
                    }, 
                    {
                        astype: "SelectField",
                        label: this.$t('Status'),
                        name: "Status",
                        value: "",
                        placeholder: "",
                        validationRules: {
                            "required": true
                        },
                        config: {
                            options: [],
                        },
                        visibility: true
                    },
                    {
                        astype: "TextField",
                        label: this.$t('AnnualRevenue'),
                        name: "AnnualRevenue",
                        value: "",
                        placeholder: this.$t('Enter Annual Revenue'),
                        validationRules: {
                            "required": false
                        },
                        disabled: false,
                    }]
                },
            ],
            buttons: [{
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    isVisible: true,

                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close,
                },
            ],
        }
    },
    created: async function () {
        var vm = this;
        vm.userId = vm.GetUserInfo.ID;
        vm.user_type= vm.GetUserInfo.UserType;
        vm.companyId = vm.GetUserInfo.CompanyId;
        await this.GetCurrencyDDL();
        await this.GetStatusList();
        await this.GetAccountProfile();
    },
    methods: {
        GetAccountProfile: async function () {
            var vm = this;
            let Url = `AccountId=${vm.$route.params.id}&userType=${vm.user_type}`
            await DataServices.GetAccountProfile(Url).then(function (response) {
                vm.AccountProfileData= response.data[0];
                if(vm.AccountProfileData){
                vm.AccountName= vm.AccountProfileData.AccountName;
                (vm.FormSchema)[0].Data[0].value= vm.AccountName;
                (vm.FormSchema)[0].Data[2].value= vm.AccountProfileData.Website;
                (vm.FormSchema)[0].Data[3].value= vm.AccountProfileData.Currency;
                (vm.FormSchema)[1].Data[0].value= vm.AccountProfileData.AccountOwner;
                (vm.FormSchema)[1].Data[1].value= vm.AccountProfileData.Status;
                (vm.FormSchema)[1].Data[2].value= vm.AccountProfileData.AnnualRevenue;
                }
            });
        },
        GetCurrencyDDL: async function () {
            var vm = this;
            await DataServices.GetCurrencyDDL().then(function (response) {
                vm.CurrencyNameddl = response.data;
                vm.CurrencyNameddl.forEach((item, index) => {
                    (vm.FormSchema)[0].Data[3].config.options.push({
                        value: item.CurrencyId,
                        name: item.Currency
                    });
                    vm.$refs.dynamicform.UpdateKeyValue();
                })
            });
        },
        async GetStatusList() {
            var vm = this;
            vm.isLoading = true;
            await DataServices.GetStatusList().then((response) => {
                if (JSON.parse(response.data) != null) {
                    vm.SubCatData = [];
                    vm.SubCatData = JSON.parse(response.data);
                    vm.SubCatData.forEach((itm, index) => {
                        if (itm.STATUS != 'Deleted') {
                            (vm.FormSchema)[1].Data[1].config.options.push({
                                name: itm.STATUS,
                                value: itm.STATUS_ID
                            })
                        }
                    })
                    vm.$refs.dynamicform.UpdateKeyValue(); 
                    vm.isLoading = false;
                }
                vm.isLoading = false;
            });
        },
        OnSubmit(formfields, val) {
            var vm = this;
            var jsonobj = {
                AccountId: vm.$route.params.id,
                AccountName: formfields.AccountName,
                AccountType: formfields.AccountType,
                Website: formfields.Website,
                Currency: formfields.Currency,
                AccountOwner: formfields.AccountOwner,
                Status:formfields.Status,
                AnnualRevenue:formfields.AnnualRevenue,
                Address: "",
                City: "",
                State: "",
                Country: "",
                ZipCode: "",
            };
            var params = JSON.stringify(jsonobj);
            DataServices.SaveAccount(params).then((response) => {
                if (response.data.Status == "Success") {
                    vm.ShowAlert(vm.$t('UpdatedSuccess', [vm.$t('Account')]), "success", true, vm.$t("Alert"));
                    vm.Close();
                }
            });
        },
        Close: function () {
            var vm=this;
            vm.$router.push({ name: 'ClientListing' });
        }

    }
}
</script>